
import Vue from 'vue'
import VueScroller from 'vue-scroller'
Vue.use(VueScroller)
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'

import axios from 'axios'
const name = window.localStorage.getItem("userName")
export default {
data() {
return {
name,
active: true,
wz: [],
personal: []
}
},
directives: {
TransferDom
},
components: {
Popup,
Group,
Cell,
XSwitch,
Toast,
XAddress,
XButton,
VueScroller
},
methods: {

refresh() {
location.reload()
},

wbdz(wbdt) {
axios.get('http://121.42.177.97:8080/redfive/blog/give.do', {
params: {
circle_id: wbdt.circle_id,
user_name: wbdt.user_name,
friend_user_id: this.personal.user_id
}
})
.then(response => {
wbdt.dzsl += 1
})
.catch(function(error) {
console.log(error);
});

},

plchange(wbdt) {
axios.get('http://121.42.177.97:8080/redfive/blog/comment1.do', {
params: {
circle_id: wbdt.circle_id,
user_name: wbdt.user_name,
friend_user_id: this.personal.user_id,
friend_comment: wbdt.circle_id,
comment_text: wbdt.input

}
})
.then(response => {
dt.plz = dt.plz.concat({
friend_nick: this.personal.user_name,
comment_text: dt.input
});
console.log(response);
wbdt.plsl += 1
})
.catch(function(error) {
console.log(error);
});

debugger
},

wbpl(wbdt) {
if(wbdt.plsl == "") {
wbdt.plsl = 0
wbdt.plsl = parseInt(wbdt.plsl) + 1
} else {
wbdt.plsl = parseInt(wbdt.plsl) + 1
}

},

weibozw(wbdt) {
wbdt.show8 = true
},

gb(wbdt) {
wbdt.show8 = false
},

plff(wbdt, userIndex) {

wbdt.pl = true;
this.$refs.plinput[userIndex].focus()
}
},
mounted() {

axios.post('http://121.42.177.97:8080/redfive/face/me.do?user_name=' + this.name)
.then((response) => {

this.personal = response.data.data
})
.catch(function(error) {
console.log(error);
});

axios.get('http://121.42.177.97:8080/redfive/blog/get_blog.do?user_name=' + this.name)
.then((response) => {

for(var i = 0; i < response.data.data.length; i++) {
response.data.data[i].pic_meta = response.data.data[i].pic_meta.split(",");
response.data.data[i].show8 = false;
response.data.data[i].wbpls = [];
response.data.data[i].wbdzs = [];
response.data.data[i].dzsl = "";
response.data.data[i].plsl = "";
}

this.wz = response.data.data

this.wz.forEach((value, index, arr) => {
console.log(value);
axios.get('http://121.42.177.97:8080/redfive/blog/get_give.do', {
params: {
user_id: this.personal.user_id,
circle_id: this.wz[index].circle_id
}
})
.then(responses => {

if(responses.data.data.length > 0) {
response.data.data[index].wbdzs = responses.data.data;
response.data.data[index].dzsl = responses.data.data.length
}
})
.catch(function(error) {
console.log(error);
});

});

this.wz.map(async(value, index) => {
const res = await axios.get(' http://121.42.177.97:8080/redfive/blog/get_comment1.do', {
params: {
user_id: this.personal.user_id,
circle_id: value.circle_id
}
})
value.wbpls = res.data.data
value.wbpls = value.wbpls.map(async(value2, index1) => {
const res1 = await axios.get('http://121.42.177.97:8080/redfive/blog/get_comment2.do', {
params: {
user_id: this.personal.user_id,
comment_id: value2.friend_comment
}
})
value2.hf = res1.data.data

this.wz[index].wbpls[index1] = value2
this.wz = [...this.wz]
})
this.wz[index] = value
})
})
.catch(function(error) {
console.log(error);
});
}

}



